mainMenu('.nav','.header__btn');
function mainMenu(menu,btn){ 
	var menu = $(menu), 
	btn = $(btn); 
	btn.on('click',function(){ 
		if ($(window).width() <= 974) {
			if(btn.hasClass('disable')){ 
				btn.removeClass('disable') 
				btn.attr("data-disable","false") 
				menu.removeClass('disable'); 
				menu.slideDown(300); 
				$(menu).css('display', 'block');
			}else{ 
				btn.addClass('disable') 
				btn.attr("data-disable","true") 
				menu.addClass('disable'); 
				menu.slideUp(300); 
			}
		}
	}) 
	$(btn).attr("data-disable","true"); 
	function toggleMenu(btn,menu){ 
		if($(btn).attr("data-disable")=="true" && $(window).width() <= 974){ 
			$(btn).addClass('disable'); 
			$(menu).addClass('disable'); 
			$(menu).hide(); 
		}else if($(window).width() > 974){ 
			$(btn).removeClass('disable'); 
			$(menu).removeClass('disable'); 
			$(menu).css('display', 'inline-block');
		}else{
			$(menu).css('display', 'block');
		}
	} 
	toggleMenu(btn,menu); 
	$(window).resize(function(){ 
		toggleMenu(btn,menu); 
	}) 
}


$('.header__btn').on('click', function(){
	if ($(window).width() <= 974) {
		$(this).toggleClass('header__btn-open');
	}else{
		$(this).removeClass('header__btn-open');
	}
})

function replaceNav(){
	if ($(window).width() <= 974) {
		$('.nav').before($('.header__social'));

		if($(window).width() <= 512){
			$('.header__social').appendTo($('.nav'));
		}

	}else{
		$('.header__social').after($('.nav'));
	}
}
replaceNav()

$(window).resize(function(){
	if ($(window).width() > 974) {
		$('.header__btn').removeClass('header__btn-open');
	}

	replaceNav();
}) 